@import url(https://fonts.googleapis.com/css?family=Monda|Sintony);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



body {
    background: #494e6b;
}

.header {
	width: 100vw;
	height: 100vh;
	top: 0;
	left: 0;
  display:flex;
  align-items: center;
  justify-content: center;
  border-bottom: 4px solid black;
  background: #494e6b;
}

.title {
  position: relative;
  display: flex;
  justify-content: flex-end;
  color: #fff;
  font-size: 4vw;
}
/* Word + SVG styles */
.word {
	position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    margin: 0 0 0.25em 0;
    cursor: default;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    grid-area: title;
    font-size: 10vw;
    font-weight: 400;
}

.title span, .word span {
	display: block;
	position: relative;
	flex: none;
	white-space: pre;
}

.word--6 {
  color: #fff;
	font-family: 'Hammersmith One', sans-serif;
}

.word--6 .char2,
.word--6 .char9 {
	color: #f7882f;
}

.shapes {
	position: absolute;
	top: 0;
	left: 0;
  width: 100vw;
  height: 100vh;
}

.wrapper {
  margin-left: 10%;
  margin-right: 10%;
  height: 100%;
}

.project {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.thumbnail {
  flex: 1 1 100%;
}

.right {
  flex: 1 1 100%;
}

.words {
  text-overflow: ellipsis;
  overflow: hidden;
  padding-bottom: 10px
}

.title {
  margin: 0px;
}

.technologies {
  border-top: 1px solid black;
  border-bottom: 1px solid black;
}

.project > * {
  flex: 1 1 100%;
}
/*
.preview {
  height: 50%;
} */

.project-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.readMoreLess {
  height: 50px;
  background: green;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media screen and (min-width: 400px) {
  .thumbnail {
     flex: 1 1 40%;  /*grow | shrink | basis */
  }

  .thumbnail img {
      display: block;
      width: 100%;
      max-width: 350px; /*actual image width*/
      height: auto; /* maintain aspect ratio*/
      margin: auto; /*optional centering of image*/
  }

.right {
  flex: 1 1 60%;
}

}

* {
  box-sizing: border-box;
}

body {
  margin: 0px;
  font-family: 'segoe ui';
}

.nav {
  height: 50px;
  width: 100%;
  background-color: #4d4d4d;
  position: relative;
}

.nav > .nav-header {
  display: inline;
}

.nav > .nav-header > .nav-title {
  display: inline-block;
  font-size: 22px;
  color: #fff;
  padding: 10px 10px 10px 10px;
}

.nav > .nav-btn {
  display: none;
}

.nav > .nav-links {
  display: inline;
  float: right;
  font-size: 18px;
}

.nav > .nav-links > a {
  display: inline-block;
  padding: 13px 10px 13px 10px;
  text-decoration: none;
  color: #efefef;
}

.nav > .nav-links > a:hover {
  background-color: rgba(0, 0, 0, 0.3);
}

.nav > #nav-check {
  display: none;
}

@media (max-width:600px) {
  .nav > .nav-btn {
    display: inline-block;
    position: absolute;
    right: 0px;
    top: 0px;
  }
  .nav > .nav-btn > label {
    display: inline-block;
    width: 50px;
    height: 50px;
    padding: 13px;
  }
  .nav > .nav-btn > label:hover {
    background-color: rgba(0, 0, 0, 0.3);
  }
  .nav > .nav-btn > label > span {
    display: block;
    width: 25px;
    height: 10px;
    border-top: 2px solid #eee;
  }
  .nav > .nav-links {
    position: absolute;
    display: block;
    width: 100%;
    background-color: #333;
    height: 0px;
    transition: all 0.3s ease-in;
    overflow-y: hidden;
    top: 50px;
    left: 0px;
  }
  .nav > .nav-links > a {
    display: block;
    width: 100%;
  }
  .nav > #nav-check:not(:checked) + .nav-links {
    height: 0px;
  }
  .nav > #nav-check:checked + .nav-links {
    height: auto;
    overflow-y: auto;
  }
}

